<template>
  <div>
    <!-- 筛选操作区 -->
    <el-row class="text-left">
      <el-col :span="4"
        >工号：<el-input v-model="userQuery.jobNumber"
          ><el-button slot="append" @click="jobNumberFilter" icon="el-icon-search"></el-button></el-input
      ></el-col>
      <el-col :span="4"
        >姓名：<el-input v-model="userQuery.userName"
          ><el-button slot="append" @click="userNameFilter" icon="el-icon-search"></el-button></el-input
      ></el-col>
      <el-col :span="4">
        部门：<el-select @change="deptSelectChange" clearable v-model="filterDept">
          <el-option
            v-for="item in deptList"
            :key="item.index"
            :label="item.label"
            :value="item.label"
          ></el-option>
        </el-select>
      </el-col>
      <el-col :span="4">
        职位：<el-select
          @change="positionSelectChange"
          clearable
          v-model="filterPosition"
        >
          <el-option
            v-for="item in positionList"
            :key="item.index"
            :value="item.positionName"
            :label="item.positionName"
          ></el-option>
        </el-select>
      </el-col>
      <el-col :offset="4" :span="2">
        <el-button
          type="primary"
          class="btn"
          @click="
            isNewDialogDisplay = true;
            this.isUpdate = false;
          "
          >新建用户</el-button
        >
      </el-col>
      <el-col :span="2">
        <el-button class="btn" type="danger" @click="removePreview">删除用户</el-button>
      </el-col>
    </el-row>
    <!-- 表单区 -->
    <el-row>
      <el-table @select="rowclick" border :data="tableData">
        <el-table-column type="selection" width="50"></el-table-column>
        <el-table-column type="index" width="50" label="序号"></el-table-column>
        <el-table-column
          v-for="item in tableList"
          :prop="item.prop"
          :width="item.width"
          :label="item.label"
          :key="item.index"
        ></el-table-column>
        <el-table-column label="操作" width="80">
          <template slot-scope="scope">
            <el-button @click="edit(scope)" type="text" size="small"
              >编辑</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </el-row>
    <!-- 翻页 -->
    <!-- 新建弹框区-->
    <el-dialog title="新建用户" id="dialog" :visible.sync="isNewDialogDisplay">
      <el-row type="flex" justify="center">
        <el-col :span="6">工号：</el-col>
        <el-col :Span="15">
          <el-input v-model="userInfor.jobNubmer"></el-input>
        </el-col>
      </el-row>
      <el-row type="flex" justify="center">
        <el-col :span="6">姓名：</el-col>
        <el-col :Span="12">
          <el-input v-model="userInfor.userName"></el-input>
        </el-col>
      </el-row>
      <el-row type="flex" justify="center">
        <el-col :span="6">部门：</el-col>
        <el-col :Span="15">
          <el-select v-model="userInfor.dept.id">
            <el-option
              v-for="item in deptList"
              :key="item.index"
              :label="item.label"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-col>
      </el-row>
      <el-row type="flex" justify="center">
        <el-col :span="6">职务：</el-col>
        <el-col :Span="15">
          <el-select @change="selectChange" v-model="userInfor.position.id">
            <el-option
              v-for="item in positionList"
              :key="item.index"
              :label="item.positionName"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-col>
      </el-row>
      <el-row type="flex" justify="center">
        <el-col :span="6">权限：</el-col>
        <el-col :Span="15">
          {{ authority }}
        </el-col>
      </el-row>
      <el-row type="flex" justify="center">
        <el-col :span="6">手机号：</el-col>
        <el-col :Span="12">
          <el-input v-model="userInfor.phoneNumber"></el-input>
        </el-col>
      </el-row>
      <el-row type="flex" justify="center">
        <el-col :span="6">密码：</el-col>
        <el-col :Span="12">
          <el-input v-model="userInfor.passWord"></el-input>
        </el-col>
      </el-row>
      <el-row type="flex" justify="center">
        <el-col :span="6">再次确认密码：</el-col>
        <el-col :Span="12">
          <el-input v-model="passWordConform"></el-input>
        </el-col>
      </el-row>
      <el-row type="flex" justify="end">
        <el-col :span="3">
          <el-button
            size="medium"
            @click="isNewDialogDisplay = false"
            type="info"
            >取消</el-button
          >
        </el-col>
        <el-col :span="3">
          <el-button size="medium" @click="addUser" type="primary"
            >确定</el-button
          >
        </el-col>
      </el-row>
    </el-dialog>
    <!-- 删除弹框区 -->
    <el-dialog
      id="dialog"
      title="删除用户"
      :visible.sync="isDeleteDialogDisplay"
    >
    <el-row style="font-size:18px">即将从系统中删除以下人员:</el-row>
    <el-row>
      <el-col style="font-size:18px" :span="6">人员：</el-col>
      <el-col :span="18">
        <div style="font-size:18px" v-for="item in selectData" :key="item.index">
          {{item.userName}}
        </div>
      </el-col>
    </el-row>
    <el-row type="flex" justify="end">
          <el-col :span="4">
            <el-button
              size="medium"
              @click="isDeleteDialogDisplay = false"
              type="info"
              >取消</el-button
            >
          </el-col>
          <el-col :span="4">
            <el-button size="medium" @click="remove(selectData)" type="primary"
              >确定</el-button
            >
          </el-col>
        </el-row>
    </el-dialog>
  </div>
</template>

<script>
import {
  getAllDeptForms,
  getAllAuthPosition,
  getAllUsers,
  InsertUser,
  GetSingleUser,
  UpdateUser,
  DeleteUsers,
} from "network/authority.js";
import {
  getAllAuthPositionProcess,
  getAllDeptFormsFilter,
  getAllUsersProcess,
} from "service/Authority/authority.js";
export default {
  data() {
    return {
      tableList: [
        { label: "工号", prop: "jobNubmer", width: "100" },
        { label: "姓名", prop: "userName", width: "100" },
        { label: "职位", prop: "position", width: "150", isFix: "left" },
        { label: "部门", prop: "dept", width: "150", isFix: "left" },
        { label: "权限", prop: "authForms", isFix: "left" },
        { label: "手机号", prop: "phoneNumber", width: "300" },
      ],
      userInfor: { dept: {}, position: {} },
      tableData: [{ sample: "ww" }],
      positionList: null,
      deptList: null,
      data: null,
      isNewDialogDisplay: false,
      isDeleteDialogDisplay: false,
      search: "",
      value: "",
      statusTypeList: [],
      filterDept: null,
      filterPosition: null,
      passWordConform: null,
      authority: null,
      userQuery: {},
      selectData:[],
    };
  },
  created() {
    var positionData = getAllAuthPositionProcess();
    this.userQuery = getAllUsersProcess();
    getAllAuthPosition(positionData).then((res) => {
      if (res.success) {
        this.positionList = res.result.items;
        this.$message.success("获取职位列表成功");
        console.log(this.positionList);
      } else {
        this.$$message.error("获取职位列表失败");
      }
    });
    getAllDeptForms().then((res) => {
      if (res.success) {
        this.deptList = getAllDeptFormsFilter(res.result.items);
        console.log(this.deptList);
      } else {
        this.$message.error("请重新刷新页面");
      }
    });
    getAllUsers(this.userQuery).then((res) => {
      if (res.success) {
        this.$message.success("获取用户列表成功");
        this.tableData = res.result;
        console.log(this.tableData);
      } else {
        this.$$message.error("获取用户列表失败");
      }
    });
  },
  methods: {
    //初始化
    //添加或更新用户
    addUser() {
      if (this.checkPassword()) {
        if (this.isUpdate) {
          UpdateUser(this.userInfor).then((res) => {
            if (res.success) {
              this.$message.success("更新用户成功");
              window.location.reload();
            }
          });
        } else {
          InsertUser(this.userInfor).then((res) => {
            if (res.success) {
              this.$message.success("添加用户成功");
            } else {
              this.$message.error("添加用户失败！");
            }
          });
          console.log(this.userInfor);
        }
      } else {
        return;
      }
    },
    //根据条件查询用户列表
    getUsers(data){
      getAllUsers(data).then((res) => {
        if (res.success) {
          this.$message.success("获取用户列表成功");
          this.tableData = res.result;
          console.log(this.tableData);
        } else {
          this.$$message.error("获取用户列表失败");
        }
      });
    },
    //工号查询
    jobNumberFilter(){
      this.getUsers(this.userQuery);
    },
    //姓名查询
    userNameFilter(){
      this.getUsers(this.userQuery);
    },
    //部门查询
    deptSelectChange(){
      if(this.filterDept==''){
        this.userQuery.deptId = 0;
      }
      else{
        this.deptList.forEach(item=>{
          if(item.label == this.filterDept){
            this.userQuery.deptId = item.id;
          }
        })
      }
      this.getUsers(this.userQuery);
    },
    //职位变化
    positionSelectChange() {
      if (this.filterPosition == '') {
                    console.log("got");
        this.userQuery.postionId = 0;
      } 
      else {
        this.positionList.forEach((item) => {
          if (item.positionName == this.filterPosition) {
            this.userQuery.postionId = item.id;
          }
        });
      }
      console.log(this.userQuery);
      this.getUsers(this.userQuery);
    },
    //校验密码
    checkPassword() {
      if (this.userInfor.passWord != this.passWordConform) {
        this.$message.error("请确保两次输入的密码一致");
        return false;
      } else {
        return true;
      }
    },
    //检查选框值改变，赋予不同权限
    selectChange() {
      console.log(this.userInfor.position.id);
      this.positionList.forEach((item) => {
        if (item.id == this.userInfor.position.id) {
          this.authority = item.authority;
          console.log(this.authority);
        }
      });
    },
    //编辑界面
    edit(scope) {
      console.log(scope);
      this.isNewDialogDisplay = true;
      this.isUpdate = true;
      GetSingleUser(scope.row.id).then((res) => {
        if (res.success) {
          console.log(res);
          this.userInfor = res.result;
          this.selectChange();
        }
      });
    },
    //点击选择框
    rowclick(selection, row){
      this.selectData = selection;
    },
    //删除用户
    removePreview(){
      if(this.selectData.length == 0){
        this.$message.error("请选择用户");
      }
      else{
        this.isDeleteDialogDisplay = true;
      }
    },
    //删除确认
    remove(selection){
      var arr = new Array(selection.length);
      selection.forEach((item,index)=>{
        arr[index] = item.id;
      });
      DeleteUsers(arr).then(res=>{
        if(res.success){
          this.isDeleteDialogDisplay = false;
          window.location.reload();
        }
        else{
          this.$$message.error("服务器报错");
        }
      })
    }
  },
};
</script>

<style scoped>
.el-input {
  width: 65% !important;
}
.el-select {
  width: 65% !important;
}
.el-row {
  margin-bottom: 10px;
}
.btn{
  width:100px
}
#dialog {
  line-height: 40px;
}
</style>